import ReactMarkdown from "react-markdown";
import rehypeRaw from "rehype-raw";
import remarkGfm from "remark-gfm";
import {Prism as SyntaxHighlighter} from "react-syntax-highlighter";
// 设置高亮样式 coldarkDark ,
import {dracula} from "react-syntax-highlighter/dist/cjs/styles/prism";
import React, {useEffect, useState} from "react";
import {Spin} from 'antd'
import axios from "axios";

function FirstLevelNavigation(props) {
  const {mainDataMd, mainDataLoading} = props;
  const [mainDataMdState,setMainDaraMdState] = useState('');
  useEffect(() => {
    async function fetchData() {
      let res = await axios({
        url: 'https://cdn.jsdelivr.net/gh/IceRain-mvc/interview/readme.md',
        method: 'GET'
      });
      setMainDaraMdState(res.data);
    }
    fetchData();
  }, []);
  useEffect(() => {
    setMainDaraMdState(mainDataMd);
  }, [mainDataMd]);
  return <>
    <Spin
      delay={'200ms'}
      tip="加载中..."
      spinning={mainDataLoading}
    >
      <ReactMarkdown
        children={mainDataMdState}
        rehypePlugins={[rehypeRaw]}
        remarkPlugins={[remarkGfm]}
        components={{
          img: ({node, ...props}) => (<img alt={'图片'} style={{width: '50%'}} {...props}/>),
          code({node, inline, className, children, ...props}) {
            const match = /language-(\w+)/.exec(className || '');
            return !inline && match ? (
              <SyntaxHighlighter
                children={String(children).replace(/\n$/, '')}
                style={dracula}
                language={match[1]}
                PreTag="div"
                {...props}
              />
            ) : (
              <code className={className} {...props}>
                {children}
              </code>
            )
          }
        }}
      >{mainDataMdState}</ReactMarkdown>
    </Spin>
  </>

}

export default FirstLevelNavigation;

