import React, {useEffect, useState} from "react";
import axios from "axios";
import {Button, List} from "antd";
function Left(props) {
  let {oneTitle, twoList, setMainDataMd, setMainDataLoading, flag} = props;
  let [activeIndex, setActiveIndex] = useState(-1);
  useEffect(() => {
    setActiveIndex(-1);
  }, [flag]);
  return <>

    <List
      itemLayout="horizontal"
      className={'list'}
      dataSource={twoList}
      renderItem={(item, index) => (
        <List.Item
          className={index === activeIndex ? 'active' : ''}
          onClick={async () => {
            if (index === activeIndex) {
              return
            }
            setActiveIndex(index);
            const url = `https://cdn.jsdelivr.net/gh/IceRain-mvc/interview@1.2/${encodeURIComponent(oneTitle)}/${encodeURIComponent(item.title)}`;
            setMainDataLoading(true);
            const mainDataMd = await axios.get(url);
            setMainDataLoading(false);
            setMainDataMd(mainDataMd.data)
          }}>
          <List.Item.Meta
            title={<Button
              type='link'
              id={index === activeIndex ? 'activeFont' : 'commonFont'}
            >{item.filterTitle||item.title}</Button>}
          />
        </List.Item>
      )}
    />
  </>

}

export default Left;
